import React, { useState } from "react";
import {Link} from 'react-router-dom';
import clsx from "clsx";
import {
  TextField,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
  FormHelperText,
  LinearProgress,
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import { useDispatch, useSelector } from "react-redux";

// Components
import { RegisterMessage } from "./Message";

// Styles
import { formStyle } from "./Style";

// Actions
import { register } from "../../actions/Register";

export default function RegisterForm({ theme }) {
  const classes = formStyle();
  const dispatch = useDispatch();

  const { loading, request_error, errors } = useSelector(
    (state) => state.register
  );
 
  const [values, setValues] = useState({
    type: "student",
    email: "",
    nic: "",
    epf: "",
    password: "",
    showPassword: false,
  });

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      register({
        type: values.type,
        email: values.email,
        nic: values.nic,
        epf: values.epf,
        password: values.password,
      })
    );
  };

  return (
    <form className="login__form" onSubmit={submitHandler}>
      <div className="login__body">
        <div className="login__title">Sign Up to your account</div>
        {request_error ? (
          <RegisterMessage message={request_error} type="error" />
        ) : null}
        <div
          className="field1 field1_select js-field active"
          style={{ marginBottom: "20px" }}
        >
          <div className="field1__label">Your Position</div>
          <div className="field1__wrap">
            <select
              className="field1__select js-field-input"
              name="type"
              onChange={handleChange("type")}
            >
              <option style={{ color: "black" }} value="student">
                Student
              </option>
              <option style={{ color: "black" }} value="staff">
                Staff Member
              </option>
              <option style={{ color: "black" }} value="company">
                Company
              </option>
            </select>
          </div>
        </div>
   <TextField
          error={errors ? (errors.email ? true : false) : false}
          className={
            theme === "light"
              ? classes.light_inputField
              : classes.dark_inputField
          }
          id="outlined-basic"
          label="Your Email"
          variant="outlined"
          fullWidth
          size="small"
          helperText={errors ? (errors.email ? errors.email : "") : ""}
          value={values.email}
          onChange={handleChange("email")}
        />     
        {values.type === "student" ? (
          <TextField
            error={errors ? (errors.nic ? true : false) : false}
            className={
              theme === "light"
                ? classes.light_inputField
                : classes.dark_inputField
            }
            id="outlined-basic"
            label="NIC Number"
            variant="outlined"
            fullWidth
            size="small"
            helperText={errors ? (errors.nic ? errors.nic : "") : ""}
            color="primary"
            value={values.nic}
            onChange={handleChange("nic")}
          />
        ) : null}
        {values.type === "staff" ? (
          <TextField
            error={errors ? (errors.epf ? true : false) : false}
            className={
              theme === "light"
                ? classes.light_inputField
                : classes.dark_inputField
            }
            id="outlined-basic"
            label="EPF Number"
            variant="outlined"
            fullWidth
            size="small"
            color="primary"
            helperText={errors ? (errors.epf ? errors.epf : "") : ""}
            value={values.epf}
            onChange={handleChange("epf")}
          />
        ) : null}

        <FormControl
          className={clsx(
            classes.margin,
            classes.textField,
            theme === "light"
              ? classes.light_inputField
              : classes.dark_inputField
          )}
          fullWidth
          size="small"
          variant="outlined"
        >
          <InputLabel htmlFor="outlined-adornment-password">
            Password
          </InputLabel>
          <OutlinedInput
            error={errors ? (errors.password ? true : false) : false}
            id="outlined-adornment-password"
            type={values.showPassword ? "text" : "password"}
            value={values.password}
            onChange={handleChange("password")}
            endAdornment={
              <InputAdornment position="end">
                <IconButton
                  style={{ zIndex: 99, color: "#B5B5BE" }}
                  aria-label="toggle password visibility"
                  onClick={handleClickShowPassword}
                  onMouseDown={handleMouseDownPassword}
                  edge="end"
                >
                  {!values.showPassword ? <Visibility /> : <VisibilityOff />}
                </IconButton>
              </InputAdornment>
            }
            labelWidth={70}
          />
          <FormHelperText
            id="outlined-weight-helper-text"
            style={{ color: "#f44336" }}
          >{errors ? (errors.password ? errors.password : "") : ""}</FormHelperText>
        </FormControl>
        <div className="login__status">
          <div className="login__progress">
            <div className="login__line" />
            <div className="login__line" />
            <div className="login__line" />
            <div className="login__line" />
            <div className="login__line" />
          </div>
          <div className="login__text">None</div>
        </div>
        <div className="login__note">
          By signing up, you confirm that you’ve read and accepted our{" "}
          <a className="login__link" href="/">
            User Notice
          </a>{" "}
          and{" "}
          <a className="login__link" href="/">
            Privacy Policy
          </a>
          .
        </div>
        <button
          className="login__btn btn btn btn_blue"
          type="submit"
          disabled={loading ? true : false}
        >
          {loading ? "Please wait" : "Register"}
        </button>
      </div>
      {loading ? <LinearProgress /> : null}

      <div className="login__foot">
        <Link className="login__link" to="/signin">
          Already have an Square account? Log in
        </Link>
      </div>
    </form>
  );
}
