import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { TextField } from "@material-ui/core";

// Styles
import { formStyle } from "./Style";

// Actions
import { registerVerification } from "../../actions/Register";

// Components
import { VerificationMessage } from "./Message";

export default function RegisterVerification({theme, props}) {
  const classes = formStyle();
  const dispatch = useDispatch();
  const { verification_token, verification } = useSelector((state) => state.register);
  const [values, setValues] = useState({ v_code: "", token: verification_token });
  const { loading, errors } = verification;

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(registerVerification(values, props));
  };

  return (
    <form className="login__form" onSubmit={submitHandler}>
      <div className="login__body">
        <div className="login__title">Email verification for your account</div>
        {errors.type === "alert" ? (
          <VerificationMessage type="error" message={errors.message} />
        ) : null}

        <TextField
          error={errors.type === "field" ? true : false}
          className={
            theme === "light"
              ? classes.light_inputField
              : classes.dark_inputField
          }
          helperText={errors.type === "field" ? errors.v_code : ""}
          id="outlined-basic"
          label="Verification Code"
          variant="outlined"
          fullWidth
          size="small"
          value={values.v_code}
          onChange={(e) => setValues({ ...values, v_code: e.target.value })}
        />
        <div className="login__note">
          By signing up, you confirm that you’ve read and accepted our{" "}
          <a className="login__link" href="/">
            User Notice
          </a>{" "}
          and{" "}
          <a className="login__link" href="/">
            Privacy Policy
          </a>
          .
        </div>
        <button
          className="login__btn btn btn btn_blue"
          type="submit"
          disabled={false}
        >
          {loading ? "Please Wait" : "Enter Verification Code"}
        </button>
      </div>
    </form>
  );
}
