// Constants
import {
  REGISTER_SUCCESS,
  REGISTER_REQUEST,
  REGISTER_FAIL,
  REGISTER_REQUEST_FAIL,
  VERIFICATION_REQUEST,
  VERIFICATION_SUCCESS,
  VERIFICATION_FAIL,
} from "../constants/Register";
import { LOGIN_SUCCESS } from "../constants/Login";

// Apis
import * as api from "../api/Register";

// Functions
import { decrypt } from "../functions/Hash";

export const register = (data) => async (dispatch) => {
  if (data.type === "student") {
    const postData = {
      type: "student",
      nic: data.nic,
      email: data.email,
      password: data.password,
    };
    dispatch({
      type: REGISTER_REQUEST,
      payload: postData,
    });
    try {
      const { data, headers } = await api.register(postData);
      const decryptedData = decrypt(data);
      if (decryptedData.has_errors) {
        dispatch({
          type: REGISTER_FAIL,
          payload: decryptedData.errors,
        });
      } else {
        if (!decryptedData.status) {
          dispatch({
            type: REGISTER_REQUEST_FAIL,
            payload: decryptedData.message,
          });
        } else {
          if (headers.token) {
            localStorage.setItem("verification_token", headers.token);
            dispatch({
              type: REGISTER_SUCCESS,
              payload: headers.token,
            });
          } else {
            dispatch({
              type: REGISTER_REQUEST_FAIL,
              payload: decryptedData.message,
            });
          }
        }
      }
    } catch (error) {
      dispatch({
        type: REGISTER_REQUEST_FAIL,
        payload: error.message,
      });
    }
  } else if (data.type === "staff") {
    const postData = {
      type: "staff",
      epf: data.epf,
      email: data.email,
      password: data.password,
    };
    dispatch({
      type: REGISTER_REQUEST,
      payload: postData,
    });
    try {
      const { data, headers } = await api.register(postData);
      const decryptedData = decrypt(data);
      if (decryptedData.has_errors) {
        dispatch({
          type: REGISTER_FAIL,
          payload: decryptedData.errors,
        });
      } else {
        if (!decryptedData.status) {
          dispatch({
            type: REGISTER_REQUEST_FAIL,
            payload: decryptedData.message,
          });
        } else {
          if (headers.token) {
            localStorage.setItem("verification_token", headers.token);
            dispatch({
              type: REGISTER_SUCCESS,
              payload: headers.token,
            });
          } else {
            dispatch({
              type: REGISTER_REQUEST_FAIL,
              payload: decryptedData.message,
            });
          }
        }
      }
    } catch (error) {
      dispatch({
        type: REGISTER_REQUEST_FAIL,
        payload: error.message,
      });
    }
  }
};

// Verification
export const registerVerification = (v_code_data, props) => async (
  dispatch
) => {
  dispatch({ type: VERIFICATION_REQUEST, payload: v_code_data.v_code });
  try {
    const { data, headers } = await api.verification(v_code_data);
    const decryptedData = decrypt(data);
    if (decryptedData.has_errors) {
      dispatch({ type: VERIFICATION_FAIL, payload: decryptedData.errors });
    } else {
      localStorage.removeItem("verification_token");
      localStorage.setItem("token", headers.token);
      dispatch({ type: VERIFICATION_SUCCESS, payload: headers.token });
      dispatch({
        type: LOGIN_SUCCESS,
        payload: headers.token,
      });
      props.history.push("/");
    }
  } catch (error) {
    dispatch({
      type: VERIFICATION_FAIL,
      payload: { type: "alert", message: error.message },
    });
  }
};
