// Constants
import {
  CHECK_LOGIN_REQUEST,
  CHECK_LOGIN_SUCCESS,
  CHECK_LOGIN_FAIL,
} from "../constants/Login";

const initState = {
  loading: false,
  is_login: false,
};

const homeReducer = (state = initState, action) => {
  switch (action.type) {
    case CHECK_LOGIN_REQUEST:
      return { ...state, loading: true, is_login: false };
    case CHECK_LOGIN_SUCCESS:
      return { ...state, loading: false, is_login: true };
    case CHECK_LOGIN_FAIL:
      return { ...state, loading: false, is_login: false };
    default:
      return state;
  }
};

export default homeReducer;
