// APIs
import * as api from "../api/Home";

// Constants
import {
  CHECK_LOGIN_REQUEST,
  CHECK_LOGIN_SUCCESS,
  CHECK_LOGIN_FAIL,
} from "../constants/Login";

// Functions
import { decrypt } from "../functions/Hash";

export const checkIsLogin = (token, props) => async (dispatch) => {
  if (token) {
    dispatch({ type: CHECK_LOGIN_REQUEST });
    try {
      const { data } = await api.checkToken({ token: token });
      const decryptData = decrypt(data);
      console.log(decryptData);
      if (decryptData.is_login) {
        dispatch({ type: CHECK_LOGIN_SUCCESS });
      } else {
        dispatch({ type: CHECK_LOGIN_FAIL });
        localStorage.removeItem("token");
      }
    } catch (error) {
      dispatch({ type: CHECK_LOGIN_FAIL });
      localStorage.removeItem("token");
    }
  } else {
    props.history.replace("/signin");
  }
};
