import React, { useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { useSelector, useDispatch } from "react-redux";
import {
  TextField,
  FormControl,
  InputLabel,
  OutlinedInput,
  InputAdornment,
  IconButton,
  FormHelperText,
  LinearProgress,
} from "@material-ui/core";
import { Visibility, VisibilityOff } from "@material-ui/icons";
import clsx from "clsx";

// Theme
import { changeTheme, addTheme } from "../../actions/Theme";
// Styles
import { formStyle } from "./Style";
// Actions
import { userLogin } from "../../actions/Login";
// Components
import { RegisterMessage } from "./Message";

export default function SignIn(props) {
  const { theme, login } = useSelector((state) => state);
  const { loading, errors } = login;
  const dispatch = useDispatch();
  const classes = formStyle();

  useEffect(() => {
    dispatch(addTheme());
  }, [dispatch]);

  const [values, setValues] = useState({
    nic: "",
    password: "",
    type: "student",
    showPassword: false,
  });

  const handleChange = (prop) => (event) => {
    setValues({ ...values, [prop]: event.target.value });
  };

  const handleClickShowPassword = () => {
    setValues({ ...values, showPassword: !values.showPassword });
  };

  const handleMouseDownPassword = (event) => {
    event.preventDefault();
  };

  const submitHandler = (e) => {
    e.preventDefault();
    dispatch(
      userLogin({
        nic: values.nic,
        password: values.password,
        type: values.type,
      },props)
    );
  };
  return (
    <div className="out">
      <div className="login">
        <div className="login__container">
          <div className="login__wrap">
            <div className="login__head">
              <a className="login__logo" href="/">
                <img className="login__pic" src="img/logo-white.svg" alt="" />
              </a>
            </div>
            <form className="login__form" onSubmit={submitHandler}>
              <div className="login__body">
                <div className="login__title login__title_sm">
                  Login to your account
                </div>
                {errors.type === 'alert' ? (
                  <RegisterMessage message={errors.message} type="error" />
                ) : null}
                <div
                  className="field1 field1_select js-field active"
                  style={{ marginBottom: "20px" }}
                >
                  <div className="field1__label">Your Position</div>
                  <div className="field1__wrap">
                    <select
                      className="field1__select js-field-input"
                      name="type"
                      onChange={handleChange("type")}
                    >
                      <option style={{ color: "black" }} value="student">
                        Student
                      </option>
                      <option style={{ color: "black" }} value="staff">
                        Staff Member
                      </option>
                      <option style={{ color: "black" }} value="company">
                        Company
                      </option>
                    </select>
                  </div>
                </div>
                <TextField
                  error={
                    errors
                      ? errors.type === "field"
                        ? errors.nic
                          ? true
                          : false
                        : false
                      : false
                  }
                  className={
                    theme === "light"
                      ? classes.light_inputField
                      : classes.dark_inputField
                  }
                  helperText={
                    errors
                      ? errors.type === "field"
                        ? errors.nic
                          ? errors.nic
                          : ""
                        : ""
                      : ""
                  }
                  id="outlined-basic"
                  label="Your NIC"
                  variant="outlined"
                  fullWidth
                  size="small"
                  value={values.nic}
                  onChange={handleChange("nic")}
                />
                <FormControl
                  className={clsx(
                    classes.margin,
                    classes.textField,
                    theme === "light"
                      ? classes.light_inputField
                      : classes.dark_inputField
                  )}
                  fullWidth
                  size="small"
                  variant="outlined"
                >
                  <InputLabel htmlFor="outlined-adornment-password">
                    Password
                  </InputLabel>
                  <OutlinedInput
                    error={
                      errors
                        ? errors.type === "field"
                          ? errors.password
                            ? true
                            : false
                          : false
                        : false
                    }
                    id="outlined-adornment-password"
                    type={values.showPassword ? "text" : "password"}
                    value={values.password}
                    onChange={handleChange("password")}
                    endAdornment={
                      <InputAdornment position="end">
                        <IconButton
                          style={{ zIndex: 99, color: "#B5B5BE" }}
                          aria-label="toggle password visibility"
                          onClick={handleClickShowPassword}
                          onMouseDown={handleMouseDownPassword}
                          edge="end"
                        >
                          {!values.showPassword ? (
                            <Visibility />
                          ) : (
                            <VisibilityOff />
                          )}
                        </IconButton>
                      </InputAdornment>
                    }
                    labelWidth={70}
                  />
                  <FormHelperText
                    id="outlined-weight-helper-text"
                    style={{ color: "#f44336" }}
                  >
                    {errors
                      ? errors.type === "field"
                        ? errors.password
                          ? errors.password
                          : ""
                        : ""
                      : ""}
                  </FormHelperText>
                </FormControl>
                <button
                  className="login__btn btn btn btn_blue"
                  type="submit"
                  disabled={loading ? true : false}
                >
                  {loading ? "Please Wait" : "Continue"}
                </button>
              </div>
              {loading ? <LinearProgress /> : null}
              <div className="login__foot">
                <Link className="login__link" to="/signup">
                  Sign up for new user?
                </Link>
              </div>
            </form>
            <div className="login__bottom">
              <ul className="login__links">
                <li>
                  <a className="login__link" href="/">
                    Privacy policy
                  </a>
                </li>
                <li>
                  <a className="login__link" href="/">
                    Terms of use
                  </a>
                </li>
              </ul>
            </div>
          </div>
        </div>
        <label className="switch switch_theme">
          <input
            className="switch__input js-switch-theme"
            type="checkbox"
            checked={theme === "light" ? false : true}
            onChange={() => dispatch(changeTheme(theme))}
          />
          <span className="switch__in">
            <span className="switch__box" />
            <span className="switch__icon">
              <svg className="icon icon-moon">
                <use xlinkHref="img/sprite.svg#icon-moon" />
              </svg>
              <svg className="icon icon-sun">
                <use xlinkHref="img/sprite.svg#icon-sun" />
              </svg>
            </span>
          </span>
        </label>
      </div>
    </div>
  );
}
