import React from "react";
// import { makeStyles } from "@material-ui/core/styles";
import { Alert} from "@material-ui/lab";
import { Collapse, IconButton } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";

// const useStyles = makeStyles((theme) => ({}));

export const RegisterMessage = ({ message, type }) => {
  //   const classes = useStyles();
  const [open, setOpen] = React.useState(true);

  return (
    <Collapse in={open} style={{ marginBottom: "20px" }}>
      <Alert
        variant="filled"
        severity={type}
        action={
          <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
              setOpen(false);
            }}
          >
            <CloseIcon fontSize="inherit" />
          </IconButton>
        }
      >
        {message}
      </Alert>
    </Collapse>
  );
};

export const VerificationMessage = ({type, message}) => {
  const [open, setOpen] = React.useState(true);
  return (
    <Collapse in={open} style={{ marginBottom: "20px" }}>
      <Alert
        variant="filled"
        severity={type}
        action={
          <IconButton
            aria-label="close"
            color="inherit"
            size="small"
            onClick={() => {
              setOpen(false);
            }}
          >
            <CloseIcon fontSize="inherit" />
          </IconButton>
        }
      >
       {message}
      </Alert>
    </Collapse>
  );
};
