import $ from "jquery";

// Constants
import { CHANGE_THEME, ADD_THEME } from "../constants/Theme";

export const changeTheme = (theme) => async (dispatch) => {
  if (theme === "light") {
    localStorage.setItem("theme", "dark");
    $("body").addClass("dark");
    dispatch({ type: CHANGE_THEME, payload: "dark" });
  } else {
    localStorage.setItem("theme", "light");
    $("body").removeClass("dark");
    dispatch({ type: CHANGE_THEME, payload: "light" });
  }
};

export const addTheme = () => async (dispatch) => {
  if (localStorage.getItem("theme")) {
    const theme = localStorage.getItem("theme");
    theme === "light"
      ? $("body").removeClass("dark")
      : $("body").addClass("dark");
    dispatch({ type: ADD_THEME, payload: theme });
  } else {
    dispatch({ type: ADD_THEME, payload: "light" });
  }
};
