import { CHANGE_THEME, ADD_THEME } from "../constants/Theme";

const initState = localStorage.getItem('theme') ? localStorage.getItem('theme') : 'light';

const reducer = (theme = initState, action) => {
  switch (action.type) {
    case CHANGE_THEME:
      return action.payload;
    case ADD_THEME:
      return action.payload;
    default:
      return theme;
  }
};

export default reducer;
