import React from "react";

export default function Header() {
  return (
    <>
      <div className="header2 js-header2">
        <button className="header2__burger js-header2-burger">
          <svg className="icon icon-burger">
            <use xlinkHref="img/sprite.svg#icon-burger" />
          </svg>
        </button>
        <a className="header2__logo" href="/">
          <img
            className="header2__pic header2__pic_black"
            src="img/logo.svg"
            alt=""
          />
          <img
            className="header2__pic header2__pic_white"
            src="img/logo-white.svg"
            alt=""
          />
        </a>
        <div className="header2__search">
          <button className="header2__open">
            <svg className="icon icon-search">
              <use xlinkHref="img/sprite.svg#icon-search" />
            </svg>
          </button>
          <input className="header2__input" type="text" placeholder="Search" />
        </div>
        <div className="header2__group">
          <button className="header2__link">
            <svg className="icon icon-friends-request">
              <use xlinkHref="img/sprite.svg#icon-friends-request" />
            </svg>
          </button>
          <button className="header2__link active js-header2-users">
            <svg className="icon icon-chat">
              <use xlinkHref="img/sprite.svg#icon-chat" />
            </svg>
          </button>
          <a
            className="header2__link header2__link_bell active"
            href="square-notifications.html"
          >
            <svg className="icon icon-bell">
              <use xlinkHref="img/sprite.svg#icon-bell" />
            </svg>
          </a>
        </div>
        <a className="header2__profile" href="square-profile.html">
          <img className="header2__pic" src="img/ava-1.png" alt="" />
        </a>
        <div className="header2__bg js-header2-bg" />
      </div>
    </>
  );
}
