// Constants
import {
  REGISTER_SUCCESS,
  REGISTER_REQUEST,
  REGISTER_FAIL,
  REGISTER_REQUEST_FAIL,
  VERIFICATION_REQUEST,
  VERIFICATION_SUCCESS,
  VERIFICATION_FAIL,
} from "../constants/Register";

const initState = {
  loading: false,
  request_error: "",
  errors: {},
  verification: {
    status: localStorage.getItem("verification_token") ? true : false,
    loading: false,
    value: "",
    errors: {},
  },
  verification_token: localStorage.getItem("verification_token"),
};

const reducer = (state = initState, action) => {
  switch (action.type) {
    case REGISTER_REQUEST:
      return { ...state, loading: true, errors: {}, request_error: "" };
    case REGISTER_SUCCESS:
      return {
        ...state,
        loading: false,
        verification_token: action.payload,
        verification: { ...state.verification, status: true },
      };
    case REGISTER_FAIL:
      return { ...state, loading: false, errors: action.payload ,verification_token: "" };
    case REGISTER_REQUEST_FAIL:
      return { ...state, loading: false, request_error: action.payload, verification_token: "" };
    case VERIFICATION_REQUEST:
      return {
        ...state,
        verification: {
          ...state.verification,
          loading: true,
          errors: {},
          value: action.payload,
        },
      };
    case VERIFICATION_SUCCESS:
      return {
        ...state,
        verification_token: "",
        verification: {
          ...state.verification,
          loading: false,
          status: false,
          errors: {}
        },
      };
    case VERIFICATION_FAIL:
      return {
        ...state,
        verification: {
          ...state.verification,
          loading: false,
          errors: action.payload,
        },
      };
    default:
      return state;
  }
};

export default reducer;
