// APIs
import * as api from "../api/Login";
// Constants
import { LOGIN_REQUEST, LOGIN_SUCCESS, LOGIN_FAIL} from "../constants/Login";

// Functions
import { decrypt } from "../functions/Hash";

export const userLogin = (values, props) => async (dispatch) => {
  dispatch({ type: LOGIN_REQUEST, payload: values });
  try {
    const { data, headers } = await api.login(values);
    const decryptedData = decrypt(data);
    if (decryptedData.has_errors) {
      dispatch({ type: LOGIN_FAIL, payload: decryptedData.errors });
    } else {
      if (decryptedData.status) {
        localStorage.setItem("token", headers.token);
        dispatch({ type: LOGIN_SUCCESS, payload: headers.token });
        props.history.replace("/");
      } else {
        dispatch({ type: LOGIN_FAIL, payload: decryptedData.errors });
      }
    }
  } catch (error) {
    dispatch({
      type: LOGIN_FAIL,
      payload: { type: "alert", message: error.message },
    });
  }
};
