import React, { useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

// Theme
import { changeTheme, addTheme } from "../../actions/Theme";

export default function Sidebar() {
  const { theme } = useSelector((state) => state);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(addTheme());
  }, [dispatch]);
  return (
    <>
      <div className="sidebar2 js-sidebar2">
        <div className="sidebar2__top">
          <button className="sidebar2__close js-sidebar2-close">
            <svg className="icon icon-close">
              <use xlinkHref="img/sprite.svg#icon-close" />
            </svg>
          </button>
          <a className="sidebar2__logo" href="/">
            <img
              className="sidebar2__pic sidebar2__pic_black"
              src="img/logo.svg"
              alt=""
            />
            <img
              className="sidebar2__pic sidebar2__pic_white"
              src="img/logo-white.svg"
              alt=""
            />
          </a>
        </div>
        <div className="sidebar2__wrapper">
          <a className="sidebar2__profile" href="square-profile.html">
            <div className="ava">
              <img className="ava__pic" src="img/ava-1.png" alt="" />
            </div>
            <div className="sidebar2__details">
              <div className="sidebar2__user">Ahmad Nur Fawaid</div>
              {/* <div className="sidebar2__login">@fawait</div> */}
            </div>
          </a>
          <div className="sidebar2__nav">
            <a className="sidebar2__item active" href="square-timeline.html">
              <svg className="icon icon-dashboard">
                <use xlinkHref="img/sprite.svg#icon-dashboard" />
              </svg>
              Dashboard
            </a>
            <a className="sidebar2__item" href="square-friends.html">
              <svg className="icon icon-friends">
                <use xlinkHref="img/sprite.svg#icon-friends" />
              </svg>
              Friends
            </a>
            <a className="sidebar2__item" href="square-event.html">
              <svg className="icon icon-event">
                <use xlinkHref="img/sprite.svg#icon-event" />
              </svg>
              Event<div className="sidebar2__counter">3</div>
            </a>
            <a className="sidebar2__item" href="square-watch-videos.html">
              <svg className="icon icon-video">
                <use xlinkHref="img/sprite.svg#icon-video" />
              </svg>
              Watch Videos
            </a>
            <a className="sidebar2__item" href="square-photos.html">
              <svg className="icon icon-photo">
                <use xlinkHref="img/sprite.svg#icon-photo" />
              </svg>
              Photos
            </a>
            <a className="sidebar2__item" href="/">
              <svg className="icon icon-file">
                <use xlinkHref="img/sprite.svg#icon-file" />
              </svg>
              Files
            </a>
            <a className="sidebar2__item" href="square-marketplace.html">
              <svg className="icon icon-sale">
                <use xlinkHref="img/sprite.svg#icon-sale" />
              </svg>
              Marketplace
            </a>
          </div>
          <div className="sidebar2__box">
            <div className="sidebar2__category">pages you like</div>
            <div className="sidebar2__menu">
              <a className="sidebar2__link" href="square-group-about.html">
                <div
                  className="sidebar2__letters"
                  style={{ backgroundColor: "#82C43C" }}
                >
                  FF
                </div>
                <div className="sidebar2__text">Football FC</div>
                <div className="sidebar2__counter">120</div>
              </a>
              <a className="sidebar2__link" href="square-group-about.html">
                <div
                  className="sidebar2__letters"
                  style={{ backgroundColor: "#A461D8" }}
                >
                  BC
                </div>
                <div className="sidebar2__text">Badminton Club</div>
              </a>
              <a className="sidebar2__link" href="square-group-about.html">
                <div
                  className="sidebar2__letters"
                  style={{ backgroundColor: "#50B5FF" }}
                >
                  UI
                </div>
                <div className="sidebar2__text">UI/UX Community</div>
              </a>
              <a className="sidebar2__link" href="square-group-about.html">
                <div
                  className="sidebar2__letters"
                  style={{ backgroundColor: "#FF9AD5" }}
                >
                  WD
                </div>
                <div className="sidebar2__text">Web Designer</div>
              </a>
            </div>
          </div>
        </div>
        <label className="switch switch_theme">
          <input
            className="switch__input js-switch-theme"
            type="checkbox"
            checked={theme === "light" ? false : true}
            onChange={() => dispatch(changeTheme(theme))}
          />
          <span className="switch__in">
            <span className="switch__box" />
            <span className="switch__icon">
              <svg className="icon icon-moon">
                <use xlinkHref="img/sprite.svg#icon-moon" />
              </svg>
              <svg className="icon icon-sun">
                <use xlinkHref="img/sprite.svg#icon-sun" />
              </svg>
            </span>
          </span>
        </label>
      </div>
    </>
  );
}
