import { makeStyles } from "@material-ui/core/styles";

export const formStyle = makeStyles((theme) => ({
    dark_inputField: {
      marginBottom: "20px",
  
      "& input": {
        color: "#92929D",
        zIndex: 99,
      },
      "& label": {
        fontSize: 15.5,
        color: "#92929D",
      },
      "& label.Mui-focused": {
        color: "#92929D",
      },
      "& .MuiInput-underline:after": {
        borderBottomColor: "#005ef4",
      },
      "& .MuiOutlinedInput-root": {
        "& fieldset": {
          borderRadius: 10,
          background: "#292932",
          border: "1px solid #292932",
        },
        "&:hover fieldset": {
          border: "1px solid #005ef4",
        },
        "&.Mui-focused fieldset": {
          border: "1px solid #005ef4",
        },
      },
    },
    light_inputField: {
        marginBottom: "20px",
  
      "& input": {
        color: "#292932",
        zIndex: 99,
      },
      "& label": {
        fontSize: 15.5,
        color: "#B5B5BE",
      },
      "& label.Mui-focused": {
        color: "#50B5FF",
      },
      "& .MuiInput-underline:after": {
        borderBottomColor: "#50B5FF",
      },
      "& .MuiOutlinedInput-root": {
        "& fieldset": {
          borderRadius: 10,
          background: "transparent",
          border: "1px solid #E2E2EA",
        },
        "&:hover fieldset": {
          borderColor: "#50B5FF",
        },
        "&.Mui-focused fieldset": {
          border: "1px solid #50B5FF",
        },
      },
    },
  }));